export default "<template>\n  <div>\n    <div class=\"section\">\n      <h1>Buttons</h1>\n      <p>\n        Our button component is super flexible. It can act as type\n        <code>button</code>, <code>a</code> tag or <code>router-link</code>.\n        Please note that you may need to use 'ButtonInput' rather than 'Button'\n        to avoid issues.\n      </p>\n\n      <pre><code>import { Button } from 'streamlabs-beaker';\n\ncomponents: {\n  Button\n}</code></pre>\n    </div>\n\n    <div class=\"section\">\n      <h2>Standard Buttons</h2>\n      <p>Used throughout website Dashboard.</p>\n      <div class=\"section\">\n        <h3>Active</h3>\n        <DemoSection title=\"Active\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-button-container s-button-container--left\">\n              <Button\n                variation=\"default\"\n                title=\"Default\"\n                tag=\"a\"\n                href=\"https://laravel.com/docs/5.8/routing\"\n              />\n\n              <Button\n                variation=\"action\"\n                title=\"Action\"\n                @click=\"buttonClick('Action')\"\n              />\n\n              <Button\n                variation=\"warning\"\n                title=\"Warning\"\n                @click=\"buttonClick('Warning')\"\n              />\n\n              <Button\n                variation=\"default\"\n                title=\"With icon\"\n                icon=\"add-circle\"\n                @click=\"buttonClick('With icon')\"\n              />\n\n              <Button\n                variation=\"default\"\n                title=\"Custom Colors\"\n                bg-color=\"pink\"\n                text-color=\"red\"\n                @click=\"buttonClick('Custom Colors')\"\n              />\n\n              <Button\n                variation=\"prime\"\n                title=\"Join Prime\"\n                icon=\"prime\"\n                @click=\"buttonClick('Join Prime')\"\n              />\n\n              <Button\n                variation=\"prime-white\"\n                title=\"Join Prime\"\n                icon=\"prime\"\n                @click=\"buttonClick('Join Prime')\"\n              />\n\n               <Button\n                variation=\"ultra\"\n                title=\"Join Ultra\"\n                icon=\"ultra\"\n                @click=\"buttonClick('Join Ultra')\"\n              />\n\n              <Button variation=\"action\" @click=\"buttonClick('Custom Slot')\">\n                <div slot=\"custom\" class=\"custom-html\">\n                  <i class=\"icon-add-circle\"></i>\n                  <span>Custom Slot</span>\n                </div>\n              </Button>\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Disabled</h3>\n        <DemoSection title=\"Disabled\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-button-container s-button-container--left\">\n              <Button\n                :variation=\"'default'\"\n                :state=\"'disabled'\"\n                :title=\"'Default'\"\n              />\n\n              <Button\n                :variation=\"'action'\"\n                :state=\"'disabled'\"\n                :title=\"'Action'\"\n              />\n\n              <Button\n                :variation=\"'warning'\"\n                :state=\"'disabled'\"\n                :title=\"'Warning'\"\n              />\n\n              <Button\n                :variation=\"'default'\"\n                :state=\"'disabled'\"\n                :title=\"'with icon'\"\n                :icon=\"'edit'\"\n              />\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Focus</h3>\n        <DemoSection title=\"Focus\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-button-container s-button-container--left\">\n              <Button\n                :variation=\"'default'\"\n                :state=\"'focused'\"\n                :title=\"'Default'\"\n              />\n\n              <Button\n                :variation=\"'action'\"\n                :state=\"'focused'\"\n                :title=\"'Action'\"\n              />\n\n              <Button\n                :variation=\"'warning'\"\n                :state=\"'focused'\"\n                :title=\"'Warning'\"\n              />\n\n              <Button\n                :variation=\"'default'\"\n                :state=\"'focused'\"\n                :title=\"'with icon'\"\n                :icon=\"'edit'\"\n              />\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Loading</h3>\n        <DemoSection title=\"Loading\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-button-container s-button-container--left\">\n              <Button\n                :variation=\"'default'\"\n                :state=\"'loading'\"\n                :title=\"'Default'\"\n              />\n\n              <Button\n                :variation=\"'action'\"\n                :state=\"'loading'\"\n                :title=\"'Action'\"\n              />\n\n              <Button\n                :variation=\"'warning'\"\n                :state=\"'loading'\"\n                :title=\"'Warning'\"\n              />\n\n              <Button\n                :variation=\"'default'\"\n                :state=\"'loading'\"\n                :title=\"'with icon'\"\n                :icon=\"'recent-events'\"\n              />\n            </div>\n          </template>\n        </DemoSection>\n\n        <p>\n          Click\n          <a @click=\"isLoadingExample = !isLoadingExample\">here</a> to test.\n        </p>\n\n        <Button\n          :class=\"{ 'is-loading': isLoadingExample }\"\n          :type=\"'button'\"\n          :variation=\"'default'\"\n          :title=\"'with icon'\"\n          :icon=\"'pop-out-1'\"\n        />\n      </div>\n    </div>\n\n    <div class=\"section\">\n      <h2>Small Buttons</h2>\n      <p>\n        Small is the size used on Streamlabs OBS. It is slightly smaller than\n        our standard button.\n      </p>\n\n      <DemoSection title=\"Small Buttons\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-button-container s-button-container--left\">\n            <Button\n              :type=\"'button'\"\n              :size=\"'small'\"\n              :variation=\"'default'\"\n              :title=\"'Default'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'small'\"\n              :variation=\"'action'\"\n              :title=\"'Action'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'small'\"\n              :variation=\"'warning'\"\n              :title=\"'Warning'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'small'\"\n              :variation=\"'default'\"\n              :title=\"'With Icon'\"\n              :icon=\"'image'\"\n            />\n\n            <Button\n              :variation=\"'rewards-standard'\"\n              :size=\"'small'\"\n              :title=\"'Rewards'\"\n            />\n          </div>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Large Buttons</h2>\n      <p>\n        Used on website marketing pages and for the Donate button on the tip\n        page.\n      </p>\n      <DemoSection title=\"Large Buttons\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-button-container s-button-container--left\">\n            <Button\n              :type=\"'button'\"\n              :size=\"'large'\"\n              :variation=\"'default'\"\n              :title=\"'Default'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'large'\"\n              :variation=\"'action'\"\n              :title=\"'Action'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'large'\"\n              :variation=\"'warning'\"\n              :title=\"'Warning'\"\n            />\n\n            <Button\n              :type=\"'button'\"\n              :size=\"'large'\"\n              :variation=\"'default'\"\n              :title=\"'With Icon'\"\n              :icon=\"'image'\"\n            />\n\n            <Button\n              :size=\"'large'\"\n              :variation=\"'prime'\"\n              :title=\"'Join Prime'\"\n              :icon=\"'prime'\"\n            />\n            <Button\n              :size=\"'large'\"\n              :variation=\"'prime-white'\"\n              :title=\"'Join Prime'\"\n              :icon=\"'prime'\"\n            />\n            <Button\n              :size=\"'large'\"\n              :variation=\"'ultra'\"\n              :title=\"'Join Ultra'\"\n              :icon=\"'ultra'\"\n            />\n          </div>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Fixed Width Buttons</h2>\n      <p>Used throughout website Dashboard. Width is fixed at 96px.</p>\n\n      <DemoSection title=\"Fixed Width Buttons\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-button-container s-button-container--left\">\n            <Button\n              type=\"button\"\n              size=\"fixed-width\"\n              variation=\"default\"\n              title=\"Default\"\n            />\n\n            <Button\n              type=\"button\"\n              size=\"fixed-width\"\n              variation=\"action\"\n              title=\"Action\"\n            />\n\n            <Button\n              type=\"button\"\n              size=\"fixed-width\"\n              variation=\"warning\"\n              title=\"Warning\"\n            />\n\n            <Button\n              type=\"button\"\n              size=\"fixed-width\"\n              variation=\"default\"\n              title=\"'WithIcon\"\n              icon=\"image\"\n            />\n          </div>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Full-Width Buttons</h2>\n      <div class=\"section\">\n        <h3>Default</h3>\n        <p>Used within cards and panels in the Dashboard.</p>\n        <DemoSection title=\"Default\" :code=\"demoCode\">\n          <template #components>\n            <Button\n              size=\"full-width\"\n              variation=\"default\"\n              title=\"Start Giveaway\"\n            />\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Subscribe</h3>\n        <p>Used on the App Platform for Subscribing to Apps.</p>\n        <DemoSection title=\"Subscribe\" :code=\"demoCode\">\n          <template #components>\n            <Button variation=\"subscribe\" title=\"Subscribe\" price=\"$4.99\" />\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Paypal</h3>\n        <p>Used on the Tip Page for Subscribing to Pro.</p>\n        <DemoSection title=\"Paypal\" :code=\"demoCode\">\n          <template #components>\n            <Button\n              variation=\"paypal\"\n              title=\"Subscribe with PayPal\"\n              price=\"$4.99\"\n            />\n          </template>\n        </DemoSection>\n      </div>\n\n      <div class=\"section\">\n        <h3>Download</h3>\n        <p>Used for Download Streamlabs OBS buttons.</p>\n        <DemoSection title=\"Download\" :code=\"demoCode\">\n          <template #components>\n            <div class=\"s-button-container s-button-container--left\">\n              <Button\n                variation=\"slobs-download\"\n                title=\"Download Streamlabs OBS\"\n              />\n              <Button\n                variation=\"slobs-download-landing\"\n                osType=\"windows\"\n              ></Button>\n              <Button variation=\"slobs-download-landing\" osType=\"mac\"></Button>\n            </div>\n          </template>\n        </DemoSection>\n      </div>\n    </div>\n    <div class=\"section\">\n      <h2>Square Buttons</h2>\n      <p>Used for add and subtract number inputs as well as login buttons.</p>\n      <DemoSection title=\"Square Buttons\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-button-container s-button-container--left\">\n            <Button\n              type=\"button\"\n              size=\"square\"\n              variation=\"default\"\n              icon=\"add\"\n            />\n\n            <Button\n              type=\"button\"\n              size=\"square\"\n              variation=\"default\"\n              icon=\"subtract\"\n            />\n\n            <Button\n              type=\"button\"\n              variation=\"facebook\"\n              size=\"square\"\n              icon=\"facebook\"\n            />\n\n            <Button\n              type=\"button\"\n              variation=\"periscope\"\n              size=\"square\"\n              icon=\"periscope\"\n            />\n\n            <Button\n              type=\"button\"\n              variation=\"mixer\"\n              size=\"square\"\n              icon=\"mixer\"\n            />\n\n            <Button\n              type=\"button\"\n              variation=\"picarto\"\n              size=\"square\"\n              icon=\"picarto\"\n            />\n\n                <Button\n              type=\"button\"\n              variation=\"ultra\"\n              size=\"square\"\n              icon=\"ultra\"\n            />\n          </div>\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Navigation Buttons</h2>\n      <p>Used to bring you between different page states.</p>\n\n      <DemoSection title=\"Navigation Buttons\" :code=\"demoCode\">\n        <template #components>\n          <Button\n            variation=\"navigation\"\n            icon=\"back-alt\"\n            title=\"Back to Listings\"\n          />\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Prime Button Text</h2>\n      <p>Used for prime button with text.</p>\n\n      <DemoSection title=\"Prime Button Text\" :code=\"demoCode\">\n        <template #components>\n          <div class=\"s-button-container s-button-container--left\">\n            <Button variation=\"prime-simple\" />\n            <Button variation=\"prime-simple\" primeTitle=\"test\" />\n          </div>\n        </template>\n      </DemoSection>\n    </div>\n\n    <table class=\"docs-table\">\n      <thead>\n        <tr>\n          <th>Prop</th>\n          <th>Type</th>\n          <th>Default</th>\n          <th>Description</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr>\n          <td>@click</td>\n          <td>Function</td>\n          <td>null</td>\n          <td>\n            Emits a click function. If you are using an event modifier such as\n            <code>prevent</code>, use <code>native</code>. For example\n            <code>@click.native.prevent</code>.\n          </td>\n        </tr>\n        <tr>\n          <td>bgColor</td>\n          <td>String</td>\n          <td>null</td>\n          <td>Used for background color.</td>\n        </tr>\n        <tr>\n          <td>textColor</td>\n          <td>String</td>\n          <td>null</td>\n          <td>Used for text color.</td>\n        </tr>\n        <tr>\n          <td>icon</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Use the endings of our custom icons. For example 'icon-settings'\n            would be 'settings'. See icons page of the design system for a full\n            list.\n          </td>\n        </tr>\n        <tr>\n          <td>iconPosition</td>\n          <td>String</td>\n          <td>left</td>\n          <td>\n            Positions the icon left or right. If positioned right, the icon will\n            have a left margin of 24px.\n          </td>\n        </tr>\n        <tr>\n          <td>iconImg</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Takes image source string to use an image as an icon. Icon image\n            will have a 14px width.\n          </td>\n        </tr>\n        <tr>\n          <td>title</td>\n          <td>String</td>\n          <td>null</td>\n          <td>Main button text.</td>\n        </tr>\n        <tr>\n          <td>price</td>\n          <td>String</td>\n          <td>null</td>\n          <td>Used for subscribe buttons in the App Store.</td>\n        </tr>\n        <tr>\n          <td>description</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Used for Streamlabs OBS download buttons to describe the version,\n            size and compatibility.\n          </td>\n        </tr>\n        <tr>\n          <td>href</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Used if the the\n            <code>type</code> is an <code>a</code> element (links).\n          </td>\n        </tr>\n        <tr>\n          <td>size</td>\n          <td>String</td>\n          <td>standard</td>\n          <td>\n            Size of the button. Options are\n            <code>small</code>, <code>large</code>, <code>square</code>,\n            <code>fixed-width</code>, and\n            <code>full-width</code>\n          </td>\n        </tr>\n        <tr>\n          <td>state</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            State of the button. Options are\n            <code>hover</code>, <code>focus</code>, <code>loading</code> and\n            <code>disabled</code>.\n          </td>\n        </tr>\n        <tr>\n          <td>type</td>\n          <td>String</td>\n          <td>null</td>\n          <td>Set to string 'submit'.</td>\n        </tr>\n        <tr>\n          <td>to</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Used if the the\n            <code>type</code> is a <code>router-link</code>. Define the path.\n          </td>\n        </tr>\n        <tr>\n          <td>tag</td>\n          <td>String</td>\n          <td>button</td>\n          <td>\n            What type of element the component is. Options are\n            <code>button</code>, <code>a</code>,\n            <code>router-link</code>\n          </td>\n        </tr>\n        <tr>\n          <td>target</td>\n          <td>String</td>\n          <td>_self</td>\n          <td>\n            Used to set different targets for\n            <code>a</code>\n            links,\n            <code>_self</code>, <code>_blank</code>, <code>_parent</code>,\n            <code>_top</code>\n          </td>\n        </tr>\n        <tr>\n          <td>primeBgColor</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            You can use this prop when you set variation as\n            <code>prime</code>. Option is only <code>white</code>.\n          </td>\n        </tr>\n        <tr>\n          <td>primeTitle</td>\n          <td>String</td>\n          <td>null</td>\n          <td>\n            Used for prime button text. You can use this prop when you set\n            variation as\n            <code>prime-simple</code>\n          </td>\n        </tr>\n        <tr>\n          <td>variation</td>\n          <td>String</td>\n          <td>default</td>\n          <td>\n            The variation style of a button. Primary options are\n            <code>default</code>, <code>action</code>, <code>prime</code> and\n            <code>warning</code>. Other options are <code>subscribe</code>,\n            <code>paypal</code>, <code>download</code>, and\n            <code>navigation</code>. Reward options are\n            <code>rewards-standard</code>, <code>rewards-silver</code>,\n            <code>rewards-gold</code>, <code>rewards-platinum</code>,\n            <code>rewards-diamond</code>, and <code>rewards-legend</code>.\n            Platform options are <code>facebook</code>, <code>mixer</code>,\n            <code>twitch</code>, <code>youtube</code>, <code>periscope</code>,\n            <code>picarto</code>and <code>paypal-blue</code>.\n          </td>\n        </tr>\n        <tr>\n          <td>slobsDownloadTitle</td>\n          <td>String</td>\n          <td>Download Streamlabs</td>\n          <td>Used for slobs download landing button title.</td>\n        </tr>\n        <tr>\n          <td>osType</td>\n          <td>String</td>\n          <td>windows</td>\n          <td>\n            Used for slobs download landing button icon. Options are\n            <code>windows</code> and <code>mac</code>.\n          </td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\nimport Accordion from \"./../components/Accordion.vue\";\nimport Button from \"./../components/Button.vue\";\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport ButtonCode from \"./Buttons.vue?raw\";\nimport { EventBus } from \"./../plugins/event-bus\";\n\n@Component({\n  components: {\n    Accordion,\n    Button,\n    DemoSection\n  }\n})\nexport default class Forms extends Vue {\n  isLoading = true;\n  isLoadingExample = false;\n  demoCode = ButtonCode;\n\n  buttonClick(buttonType) {\n    EventBus.$emit(\"copy-success\", `\"${buttonType}\" button clicked`);\n  }\n}\n</script>\n\n<style lang=\"less\" scoped>\n.custom-html {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  width: 150px;\n}\n</style>\n"